import React, {useEffect, useState} from 'react';
import Search from './components/Search';
import AuthorList from './components/AuthorList';
import './App.css';
import data from './data/data.json';
import Pagination from "./components/common/Pagination";

const App = () => {
    const [authors, setAuthors] = useState([]);
    const [search, setSearch] = useState('');
    const [searchResult, setSearchResult] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [authorPerPage] = useState(10);
    const [sortType, setSortType] = useState('pageviews');

    sortByPageView();

    // Get current author
    const indexOfLastAuthor = currentPage * authorPerPage;
    const indexOfFirstAuthor = indexOfLastAuthor - authorPerPage;
    const currentAuthors = data.slice(indexOfFirstAuthor, indexOfLastAuthor);
    const totalAuthorsCount = data.length;

    function sortByPageView() {
        data.sort((a, b) => b.pageviews - a.pageviews)
            .forEach((author, id) => {
                author.position = id + 1;
                author.iconColor = randomHex();
            })
    }

    function prevArrow() {
        if (currentPage > 1) {
            setCurrentPage(currentPage - 1)
        }
    }

    function nextArrow() {
        if (currentPage < numOfPages()) {
            setCurrentPage(currentPage + 1)
        }
    }

    function numOfPages() {
        return Math.ceil(totalAuthorsCount / authorPerPage)
    }

    function randomHex() {
        return '#' + Math.floor(Math.random() * 16777215).toString(16);
    }

    const handleChange = e => {
        setSearch(e.target.value);
    }

    useEffect(() => {
        setSearchResult(author => {
                return data.filter(author => author.name.toLowerCase().includes(search.toLowerCase()));
            }
        )
    }, [search, data])

    useEffect(() => {
        const sortAuthors = type => {
            const types = {
                name: 'name',
                pageviews: 'pageviews'
            };
            const sortProperty = types[type];

            if (type === 'name') {
                const sorted = [...currentAuthors].sort((a,b) => a[sortProperty].localeCompare(b[sortProperty]));
                console.log(sorted);
                setAuthors(sorted);
            }
            if (type === 'pageviews') {
                const sorted = [...currentAuthors].sort((a, b) => b[sortProperty] - a[sortProperty]);
                console.log(sorted);
                setAuthors(sorted);
            }
        }

        sortAuthors(sortType);
    }, [sortType])

    return (
        <div className='app'>
            <select onChange={e => setSortType(e.target.value)}>
                <option defaultValue='pageviews' value='name'>by name</option>
                <option value='pageviews'>by pageviews</option>
            </select>
            <div className='container'>
                <main className='main'>
                    <Search search={search}
                            handleChange={handleChange}
                            searchResult={searchResult}
                    />
                    <AuthorList currentAuthors={search ? searchResult : authors}/>
                </main>
            </div>
            <Pagination currentPage={currentPage}
                        authorPerPage={authorPerPage}
                        currentAuthors={currentAuthors}
                        totalAuthors={data.length}
                        prevArrow={prevArrow}
                        nextArrow={nextArrow}
                        lastPage={numOfPages}
            />
        </div>
    );
}

export default App;
